<script>
import Header from "../../components/global/header/HeaderArea2.vue"
import CalendarModule from '../order/components/CalendarModule.vue'
import { ref, onMounted } from 'vue'
import { useRouter } from "vue-router"
import { Toast } from '../../components/global/swal'
import { timeHandle, addZero, getUrlId, errorHandle } from '@/utils/handle'
import { apiGetCareRecipientList, apiGetServiceList, apiPutWorkOrderInfo, apiGetWorkOrderInfo, apiGetCaretakerList, apiGetJobType } from "@/api/api"
import { useJobStore } from '@/store/global'
import { focusInput } from '@/utils/fixFooter'
// import { ExtCall } from "@/utils/extCall"

export default {
  setup() {
    const jobStore = useJobStore()
    const urlId = getUrlId().urlId
    const urlIdLink = `/order/info/${urlId}`
    const router = useRouter()
    const postData = ref({
      "isPublic": 1,
      "isFullDay": 0,
      "disabledPersonId": undefined,
      "serviceId": undefined,
      "workdays": [],
      "workDate": [],
      "startTime": undefined,
      "endTime": undefined,
      "caretakerResumeId": []
    })
    let favData = ref([])
    let inviteData = ref([])
    const careRecipientList = ref([])
    const requireList = ref([])
    // 時間區間
    const timeList = []
    timeHandle(timeList)

    //jobtype
    let jobTypeList = []
    let jobType = {}
    let jobTypeId = 1

    onMounted(async () => {
      focusInput()
      //被照顧者、服務需求列表
      careRecipientList.value = await apiGetCareRecipientList({ page: 1, limit: 100 })
      careRecipientList.value = careRecipientList.value.data.data
      requireList.value = await apiGetServiceList({ page: 1, limit: 100 })
      requireList.value = requireList.value.data.data
      //order資料
      let data = await apiGetWorkOrderInfo({ id: urlId })
      data = data.data
      postData.value.disabledPersonId = data.disabledPerson.disabledPersonId
      postData.value.serviceId = data.employerService.id
      //日期
      data.workdays.forEach(item => {
        item.start = new Date(item.start.replace(/-/g, '/'))
        item.end = new Date(item.end.replace(/-/g, '/'))
        postData.value.workDate.push(`${item.start.getFullYear()}-${addZero(item.start.getMonth() + 1)}-${addZero(item.start.getDate())}`)
      })
      //時間

      if (data.workdays[0].start.getHours() == data.workdays[0].end.getHours()) {
        postData.value.isFullDay = 1
        postData.value.startTime = `${data.workdays[0].start.getHours()}:${addZero(data.workdays[0].start.getMinutes())}`
      } else {
        postData.value.isFullDay = 0
        postData.value.startTime = `${data.workdays[0].start.getHours()}:${addZero(data.workdays[0].start.getMinutes())}`
        postData.value.endTime = `${data.workdays[0].end.getHours()}:${addZero(data.workdays[0].end.getMinutes())}`
      }
      //已邀約看護
      data.careTakerList.forEach(item => {
        postData.value.caretakerResumeId.push(item.id)
      })
      await updateJobTypeId()
      favData.value = await apiGetCaretakerList({ page: 1, limit: 100, isFavorite: 1, jobTypeId: jobTypeId })
      favData.value = favData.value.data.data
      inviteData.value = favData.value.filter(x => postData.value.caretakerResumeId.includes(x.id))

      // console.log(data)
      // console.log(postData.value)
    })


    const updateJobTypeId = async (value) => {
      if (jobStore.jobTypeList == undefined) {
        // call api
        jobTypeList = await apiGetJobType()
        jobTypeList = jobTypeList.data.data
        jobStore.jobTypeList = jobTypeList.value
      } else {
        jobTypeList = jobStore.jobTypeList
      }
      jobType = requireList.value.filter(x => x.id == postData.value.serviceId)
      jobTypeId = jobTypeList.filter(x => x.name == jobType[0].name)
      jobTypeId = jobTypeId[0].id
    }
    const updateWorkDate = (val) => {
      postData.value.workDate = val
      // console.log(postData.value.workDate)
    }

    const save = async () => {
      //處理workdays
      if (postData.value.isFullDay == 0) {
        postData.value.workDate.forEach(x => {
          postData.value.workdays.push({
            "start": `${x} ${postData.value.startTime}:00`,
            "end": `${x} ${postData.value.endTime}:00`
          })
        })
      } else {
        postData.value.workDate.forEach(x => {
          let tomorrow = new Date(Date.parse(x) + 1000 * 60 * 60 * 24)
          postData.value.workdays.push({
            "start": `${x} ${postData.value.startTime}:00`,
            "end": `${tomorrow.getFullYear()}-${tomorrow.getMonth() + 1}-${tomorrow.getDate()} ${postData.value.startTime}:00`
          })
        })
      }
      // console.log(postData.value)
      let msg = await apiPutWorkOrderInfo({ id: urlId }, postData.value)
      if (msg.status == true) {
        Toast("修改成功")
        router.push({ path: urlIdLink })
      } else {
        let errorMsg = errorHandle(msg)
        Toast(errorMsg)
      }
    }

    return {
      urlIdLink,
      save, careRecipientList, requireList, postData, timeList,
      favData, inviteData, updateWorkDate
    }
  },
  components: {
    Header, CalendarModule
  },
}
</script>

<template>
  <Header title="編輯工單" :path=urlIdLink />
  <div class="orderAdd">
    <div class="title">
      <p>編輯工單</p>
    </div>
    <hr>
    <div class="mb-3">
      <label class="form-label">被照護者<span class="star">*</span></label>
      <select class="form-control form-select" v-model=postData.disabledPersonId required>
        <option value=undefined>請選擇被照護者</option>
        <option v-for="item in careRecipientList" :key="'A' + item.id" :value="item.id">{{ item.name }}</option>
      </select>
    </div>
    <div class="mb-3">
      <label class="form-label">服務需求<span class="star">*</span></label>
      <select class="form-control form-select" v-model=postData.serviceId disabled required>
        <option value=undefined>請選擇服務需求</option>
        <option v-for="item in requireList" :key="'B' + item.id" :value="item.id">{{ item.name }}({{ item.createTime }})
        </option>
      </select>
    </div>
    <div class="mb-3">
      <label class="form-label">照顧日期<span class="star">*</span></label>
      <CalendarModule @update-work-date="updateWorkDate" :date_p='postData.workDate'
        v-if="(postData.workDate.length > 0)" />
    </div>
    <div class="mb-3">
      <label class="form-label">是否為24小時制<span class="star">*</span></label>
      <div class="input-group">
        <div class="col form-check">
          <input type="radio" class="form-check-input" id="yes" value=1 v-model=postData.isFullDay>
          <label class="form-check-label" for="yes">是</label>
        </div>
        <div class="col form-check">
          <input type="radio" class="form-check-input" id="no" value=0 v-model=postData.isFullDay>
          <label class="form-check-label" for="no">否</label>
        </div>
      </div>
    </div>
    <div class="mb-3">
      <label class="form-label">照顧時段<span class="star">*</span></label>
      <div class="input-group" v-if="(postData.isFullDay == 0)">
        <select class="col form-control form-select" v-model=postData.startTime>
          <option value="undefined" disabled>請選擇開始時間</option>
          <option v-for="item in timeList" :key="'C' + item.id" :value="item.time">{{ item.time }}</option>
        </select>
        <select class="col form-control form-select" v-model=postData.endTime>
          <option value="undefined" disabled>請選擇結束時間</option>
          <option v-for="item in timeList" :key="'D' + item.id" :value="item.time">{{ item.time }}</option>
        </select>
      </div>
      <div class="input-group" v-if="(postData.isFullDay == 1)">
        <select class="col form-control form-select" v-model=postData.startTime>
          <option value="undefined" disabled>請選擇開始時間</option>
          <option v-for="item in timeList" :key="'C' + item.id" :value="item.time">{{ item.time }}</option>
        </select>
      </div>
    </div>
  </div>
  <footer class="c-footer">
    <div class="ui-tabbtn">
      <div class="row">
        <div class="col">
          <button class="btn btn-primary" type="button" @click="save">確定修改</button>
        </div>
      </div>
    </div>
  </footer>
</template>


<style lang="scss" scoped>
@import "@/assets/custom/config";


Header {
  background-color: #FFFFFF;
}

.orderAdd {
  margin-bottom: calc($footer-height + 15px);
  margin-top: $header-height;
  padding: 1.25rem;
}

.title {
  font-size: 1.25rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
}

.modal-body {
  max-height: 50vh;
  overflow-y: scroll;
}

.modal-body::-webkit-scrollbar {
  display: none;
}

.checkBtn {
  margin: 1rem;
}

.form-check-inline {
  margin-left: 1rem;
}

.noDataAlert {
  font-size: 1.25rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
  margin: 0.8rem;
}
</style>